import React, { Component } from 'react';
import { Menu, Button, Drawer, Icon } from "antd";
import Link from 'next/link'

class MobileMenu extends Component {
  state = { visible: false }

  render() {
    const { visible } = this.state;
    return (
      <div className="mobile-menu">
        <Button type="link" onClick={() => this.setState({ visible: true })}>
          <Icon type="menu" />
        </Button>

        <Drawer
          placement="right"
          closable={true}
          onClose={() => this.setState({ visible: false })}
          visible={visible}
        >
          <Menu
            theme="light"
            mode="vertical-right"
            defaultSelectedKeys={['home']}
            onClick={this.onMenuClick}
            className="mobile-menu__menu"
          >
            <Menu.Item key="/">
              <Link href="/">
                <a>
                  Anasayfa
                </a>
              </Link>
            </Menu.Item>
            <Menu.Item key="/kurumsal">
              <Link href="/kurumsal">
                <a>
                  Kurumsal
                </a>
              </Link>
            </Menu.Item>
            <Menu.Item key="promotion">
              NetFinans Nedir?
            </Menu.Item>
            <Menu.Item key="features">
              Özellikler
            </Menu.Item>
            <Menu.Item key="/blog">
            <Link href="https://netfinans.com/blog">
                <a>
                  Blog
                </a>
              </Link>
            </Menu.Item>
            <Menu.Item key="/iletisim">
              <Link href="/iletisim">
                <a>
                  İletişim
                </a>
              </Link>
            </Menu.Item>
          </Menu>
        </Drawer>
      </div>
    );
  };
};

export default MobileMenu;